.p-sec-gallery__cont{
    grid-gap: var(--spacing-x-s);
}

.p-sec-gallery__item{
    text-align: center;
}

.p-sec-gallery__item img{
    filter: grayscale(100%);
    transition: all 0.4s ease-in-out;
}

.p-sec-gallery__item img:hover{
    filter: grayscale(0);
}

@media screen and (max-width: 1024px) {
    .p-sec-gallery__item img{
        filter: grayscale(0%);
    }
}